import React from "react"
import {BrowserRouter, Route, Switch} from "react-router-dom";
import Index from "./pages/index/indexContainer"
// import About from "./pages/about/indexContainer"
import Event from "./pages/events/indexContainer"

const Body = (props) => {
    return (
        <BrowserRouter>
            <Switch>
                <Route exact path={"/about"} render={() => <Index/>}/>
                <Route strict path={"/events"} render={() => <Event/>}/>
                <Route strict path={["/"]} render={() => <Index/>}/>
            </Switch>
        </BrowserRouter>
    )
}

export default Body