import React from "react";
import Index from './index'
import {BrowserRouter, Route, Switch} from "react-router-dom";

const IndexContainer = (props) => {
    return (
        <BrowserRouter>
            <Switch>
                <Route exact path={"/events/:year/:name"} render={(props) => <Index year={props.match.params.year} folder={props.match.params.name}/>}/>
                <Route strict path={["/events"]} render={() => <Index/>}/>
            </Switch>
        </BrowserRouter>
    )
}

export default IndexContainer